import { Component } from 'react';
import { connect } from 'react-redux';
import Layer from '../components/Layer';

class LayerContainer extends Component {
  componentDidMount() {
    this.ctx = this.canvas.getContext('2d');
  }

  clear() {
    this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
  }

  getImageData() {
    return this.ctx.getImageData(0, 0, this.canvas.width, this.canvas.height).data;
  }

  putImageData(data) {
    const { width, height } = this.canvas;
    for (let y = 0; y < height; y += 1) {
      for (let x = 0; x < width; x += 1) {
        const pos = (y * width + x) * 4;
        const pixel = data.slice(pos, pos + 4);
        this.ctx.fillStyle = `rgba(${pixel.join(',')})`;
        this.ctx.fillRect(x, y, 1, 1);
      }
    }
  }

  slowAndSafePutImageData(data) {
    const { width, height } = this.canvas;
    for (let y = 0; y < height; y += 1) {
      for (let x = 0; x < width; x += 1) {
        const pos = (y * width + x) * 4;
        const [r, g, b, a] = [
          data[pos],
          data[pos + 1],
          data[pos + 2],
          data[pos + 3] / 255,
        ];
        this.ctx.fillStyle = `rgba(${r},${g},${b},${a})`;
        this.ctx.fillRect(x, y, 1, 1);
      }
    }
  }

  render() {
    return (
      <Layer
        width={this.state.width}
        height={this.state.height}
        scale={this.state.scale}
        ref={c => this.canvas = c}
      />
    );
  }
}

const mapStateToProps = state => ({
  scale: state.counter.scale,
  width: state.counter.width,
  height: state.counter.height,
});

export default connect(mapStateToProps)(LayerContainer);



